/*APP COMPONENT RELATED CSS GOES HERE*/
body {
    margin: 0;
    padding: 0;
}


.App {
	background-color: #E9EBEE;
  padding: 0 0 9rem 0;
}


.header {
	background-color: #008b38;
}

.icon-span {
  padding: 0 0 0 0.5rem;
}

.fa-graduation-cap {
	color: #FFF;
}
.app-site-title {
	margin: 0 auto 2rem;
  color: #FFF;
  font-size: 4rem;
  width: 46%;
  text-align: center;
  font-family: 'Arvo', serif;
  padding: 1rem 0 1rem 0;
}

/*font-family: 'Roboto Slab', serif;
font-family: 'Noto Serif', serif;
font-family: 'Arvo', serif;
font-family: 'Open Sans', sans-serif;*/


.CompareCard {
	background-color: #E9EBEE;
	display: flex;
	justify-content: center;
  padding: 0 0 1.5rem 0;
  margin: auto;
  width: 50%;
}

.average-card {
	align-items: center;
	background-color: #FFF;
	border-radius: 10px;
	box-shadow: 0px 0px 1px 2px #F5F6F7;
	cursor: pointer;
	display: flex;
	flex-direction: column;
	height: 26rem;
	justify-content: space-around;
	margin: 0 2rem 0.5rem;
	padding: 0 0 1rem;
	text-align: center;
	width: 15rem;
}

.location {
  font-family: 'Arvo', serif;
  margin: 1rem 2rem 0;
}

.average {
	background-color: #F5F6F7;
	border-radius: 10px;
	color: #01411b;
	font-family: 'Open Sans', sans-serif;
	font-weight: 300;
	margin: 0.2rem auto;
	padding: 0 0.75rem;
}

.compared {
	color: #087ff5;
  font-family: 'Arvo', serif;
	font-size: 1.5rem;
}

.label-average, .label-location, .label-compared {
	color: #026332;
	font-family: 'Open Sans', sans-serif;
	font-size: 1rem;
}
.DistrictCard {
	align-items: center;
	background-color: #FFF;
	border-radius: 10px;
	box-shadow: 0px 0px 1px 2px #F5F6F7;
	cursor: pointer;
	display: flex;
	flex-direction: column;
	font-family: 'Open Sans', sans-serif;
	height: 26rem;
	justify-content: center;
	margin: 0 auto 0.5rem;
	padding: 0 0 1rem;
	text-align: center;
	width: 15rem;
}

.card-location {
	font-family: 'Arvo', serif;
	font-size: 1.25rem;
	margin: 1.5rem 1.5rem 0.75rem;
	overflow-wrap: break-word;
	text-align: center;
}


.greater-than-point-5 {
	background-color: #F5F6F7;
	border-radius: 10px;
	color: #01411b;
	font-weight: 300;
	margin: 0.2rem auto;
	padding: 0 0.75rem;
}

.less-than-point-5 {
	background-color: #008b38;
	border-radius: 10px;
	color: #FFF;
	font-weight: 300;
	margin: 0.2rem auto;
	padding: 0 0.75rem;
}

.card-highlight {
	align-items: center;
	background-color: #FFF;
	border-radius: 10px;
	box-shadow: 0px 0px 1px 4px #087ff5;
	cursor: pointer;
	display: flex;
	flex-direction: column;
	font-family: 'Open Sans', sans-serif;
	height: 26rem;
	justify-content: center;
	margin: 0 auto 0.5rem;
	padding: 0 0 1rem;
	text-align: center;
	width: 15rem;
}
.DistrictsContainer {
	background-color: #E9EBEE;
	display: grid;
	grid-gap: 0.75rem;
	grid-template-columns: repeat(5, 1fr);
	margin: 0 5rem;
}


.Search {
	align-items: center;
	display: flex;
	justify-content: center;
	width: 100%;
}

.search-input {
	border: none;
  border-radius: 10px;
	box-shadow: 0px 0px 1px 4px #e1e1e1;
	font-size: 1.5rem;
  height: 3rem;
  margin: 0 0 2rem 0;
  padding: 0 0 0 0.5rem;
	width: 25rem;
}
